<template>
    <nav class="page-nav">
        <div class="nav-btn" :class="{ 'active':  NAVBTN_ACTIVED}" @click.stop="navToggle">
            <span></span>
            <span></span>
            <span></span>
        </div>

        <transition name="page-nav">
            <nav class="page-nav-box" v-show="NAVBTN_ACTIVED">
                <ul class="page-nav-ul">
                    <li class="page-nav-li" 
                    :class="{'actived': NAV_ACTIVE_INDEX == index+1}"
                    v-for="(item, index) in navlist"
                    :key="`nav-li-${index}`"
                    @click.stop="goTo(index, item.router_path)"
                    >
                        <div class="nav-li-bg">
                            <p>{{ item.text_en }}</p>
                        </div>
                        <div class="nav-li-box">
                            <div class="nav-li-slide"></div>
                            <div class="nav-li-title">
                                <p>{{ item.text_jp }}</p>
                            </div>
                        </div>
                    </li>
                </ul>
            </nav>
        </transition>
    </nav>
</template>

<script>
import {mapState, mapGetters} from 'vuex'

export default {
    name: "Nav",
    props: {
        navlist: {      // 导航栏列表
            type: Array,
            required: true
        },
        fullpage:{      // 全屏滚动对象
            required: false
        },    
    },
    components: {

    },
    data: () => ({
    }),
    created() {
    },
    mounted() {
    },
    watch: {
    },
    computed: {
        ...mapState({
            // 导航栏当前开启状态
            NAVBTN_ACTIVED: state => state.Nav.NAVBTN_ACTIVED,
        }),
        NAV_ACTIVE_INDEX() {    // 导航栏当前页签标识
            if(this.fullpage) {
                return this.fullpage.activeIndex
            }
        },
    },
    methods: {
        navToggle() {
            // 切换导航栏开启、关闭状态
            this.$store.commit("Nav/SET_NAVBTN_ACTIVED", !this.NAVBTN_ACTIVED)
        },
        goTo(index, router_path) {
            // 页签导航
            if(router_path) {
                this.$router.push({ path: router_path })
                this.$store.commit("Nav/SET_NAVBTN_ACTIVED", false);
            } else if(this.fullpage) {
                this.fullpage.goTo(index+1)
                this.$store.commit("Nav/SET_NAVBTN_ACTIVED", false);
            } else {
                this.$router.push({ name: 'home', params: { 'navIndex':index } })
            }
        }
    },
};
</script>

